import React from 'react';
import './style.css';
import SteamIcon from '../../steam_icon.png';
import Draggable, {DraggableCore} from 'react-draggable'; // Both at the same time

class Aba extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        open: this.props.open
    }
  }
  Close(){
    this.setState(state => ({
        open: !state.open
    }));
  }
  render(){
    return <div>
    <Draggable>
      <div className="aba">
          <div className="header">
            <span className="title">
            <img src={SteamIcon}/>
            { this.props.title }
            </span>    
            
          </div>
          <div className="data">
              { <this.props.data/> }
          </div>
      </div>
      </Draggable>
    </div>
  };
}

export default Aba;