import React from 'react';
import './style.css';

const Help = `
    Available commands:
    help,
    clear,
    owner
`

const Created = `
    Created by Kaway404, SNOOP
`

class Console extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        value: '',
        exec: []
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleSubmit(event){
    event.preventDefault();
    if(this.state.value.trim().length > 0){
    let items = [...this.state.exec];
    if(this.state.value == "help"){
        items.push({
            "title": this.state.value,
            "desc": "All commands:",
            "data": Help
        })
    }
    if(this.state.value == "owner"){
        items.push({
            "title": this.state.value,
            "desc": "Who raised me?:",
            "data": Created
        })
    }
    if(this.state.value == "clear"){
        items = []
    }
    this.setState({exec: items});
    this.setState({value: ''})
    }
  }
  handleChange(event) {
    this.setState({value: event.target.value});
  }
  render(){
    return <div>
        <div className="console">
            <div className="command">
                { this.state.exec.map((item, index) => (
                    <div key={index}>
                    <span> {item.title} - {item.desc} <br/> {item.data}
                    </span>
                    </div>
                ))}
            </div>
            <div className="bottom">
            <form onSubmit={this.handleSubmit}>
                <input 
                value={this.state.value}
                onChange={this.handleChange}
                type="text" placeholder="Command"/>
            </form>
            </div>
        </div>
    </div>
  };
}

export default Console;